@import url(https://fonts.googleapis.com/css2?family=Nunito:wght@300&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@800&display=swap);
body {
    color: #000;
}

body * {
    font-family: Nunito;
}

.navbar {
    background: rgb(0, 0, 0, .95) !important;
}
.navbar li {
    padding: .2rem 1rem;
}
.navbar li a {
    color: #fff !important;
    transition: .1s;
}
.navbar li a:hover {
    color: rgb(113, 194, 221) !important;
    text-decoration: none !important;
}
.sticky {
    position: fixed;
    padding: 2rem 1rem;
    transition: .5s;
}

a.btn {
    padding: .5rem 5rem;
}
button.btn {
    padding: .5rem 1rem;
}
.btn {
    font-weight: 1000;
    transition: .3s;
}
.btn-primary {
    background-color: #0246d8 !important;
}
.btn-success {
    background-color: #0aaa0a !important;
    font-size: 120%;
}
.btn:hover {
    transform: translateY(-5%);
}
.btn-light:hover {
    background-color: #ffffff !important;
}
.btn-secondary:hover {
    background-color: #0246d8 !important;
}

.headers {
    height: 100vh;
}
.headers .col {
    padding: 0 !important;
}
.headers p, .headers span {
    font-family: Poppins;
    font-size: 300%;
    text-shadow: 2px 2px #000;
    font-weight: 1000;
}
.headers:not(.aboutHeader) p, .headers:not(.aboutHeader) span {
    background: rgb(0, 0, 0, .3);
}
.headers p span {
    font-size: 90% !important;
}

.each-slide div span {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%,-50%);
}

@media only screen and (max-width: 992px) {
    .each-slide div span {
        width: 95%;
    }
}

.react-slideshow-container button {
    display: none !important;
}

.progress-bar {
    background-color: none !important;
}

.gallery {
    position: relative;
    height: 100%;
    cursor: pointer;
}
.imgHover {
    position: absolute;
    left: 0;
    top: 0;
    height: 100%;
    width: 100%;
    background: rgb(22, 22, 235);
    opacity: 0;
    transition: .5s;
}
.galleryContent {
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
}
.galleryContent h4 {
    font-family: Poppins;
    font-size: 200%;
}
.galleryContent span {
    font-size: 150%;
    line-height: 5px;
}
@media only screen and (max-width: 992px) {
    .galleryContent h4 {
        font-size: 150%;
    }
    .galleryContent span {
        font-size: 120%;
    }
}
.gallery:hover .imgHover {
    opacity: .9;
}

.service img {
    height: 200px;
}
.service {
    cursor: default;
    transition: .3s;
}
.service:hover {
    transform: translateY(-1rem);
}

.testimonial {
    cursor: default;
    transition: .3s;
}
.testimonial:hover {
    background: #0275d8;
    color: white;
    transform: scale(1.1);
}

.statSpan {
    font-size: 120%;
}

.employee img {
    width: 200px;
    height: 200px;
    margin: auto;
    border-radius: 50%;
}

.position {
    padding: 2.5em !important;
}

.contactHeader {
    height: 60vh;
}
.contactCards span {
    font-size: 120%;
}

.form-group * {
    font-size: 115%;
}

footer {
    background: rgb(0, 0, 0);
    padding: 5rem 5rem 2rem 5rem;
}
footer p {
    font-size: 130%;
}

.NotFound {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100vh;
    z-index: 999999999999;
    background: #ecdcec;
}
